<template>
  <div class="md-example-child md-example-child-toast">
    <md-button class="button" inline small @click="showTextToast"></md-button>
    <md-button class="button" inline small @click="showSucceedToast"></md-button>
    <md-button class="button" inline small @click="showFailedToast"></md-button>
    <md-button class="button" inline small @click="showLoadingToast"></md-button>
    <md-button class="button" inline small @click="showMultiToast"></md-button>
    <md-button class="button" inline small @click="showLoginTextToast"></md-button>
  </div>
</template>

<script>
import Button from 'mand-mobile/lib/button'
import Toast from 'mand-mobile/lib/toast'

export default {
  name: 'toast-demo',
  components: {
    'md-button': Button,
  },
  methods: {
    showTextToast() {
      Toast.info('')
    },
    showSucceedToast() {
      Toast.succeed('')
    },
    showFailedToast() {
      Toast.failed('')
    },
    showLoadingToast() {
      Toast.loading('...')
      setTimeout(() => {
        Toast.hide()
      }, 3000)
    },
    showMultiToast() {
      Toast.loading('')
      setTimeout(() => {
        Toast.hide()
        setTimeout(() => {
          Toast.failed('')
        }, 10)
      }, 500)
    },
    showLoginTextToast() {
      Toast.succeed('15')
    },
  },
}

</script>

<style scoped>
.button {
  margin: 10px;
}
</style>
