<template>
  <div class="md-example-child md-example-child-selector md-example-child-selector-3">
    <md-field>
      <md-field-item
        title="Check"
        :addon="selectorValue"
        @click="showSelector"
        arrow
      />
    </md-field>
    <md-selector
      v-model="isSelectorShow"
      :data="data[0]"
      title="Check"
      min-height="320px"
      okText=""
      cancelText=""
      @confirm="onSelectorConfirm"
      is-check
    ></md-selector>
  </div>
</template>

<script>
import Selector from 'mand-mobile/lib/selector'
import Field from 'mand-mobile/lib/field'
import FieldItem from 'mand-mobile/lib/field/item'

export default {
  components: {
    'md-selector': Selector,
    'md-field': Field,
    'md-field-item': FieldItem,
  },
  data() {
    return {
      isSelectorShow: false,
      data: [
        [
          {
            value: '1',
            text: '',
          },
          {
            value: '2',
            text: '',
          },
          {
            value: '3',
            text: '',
            disabled: true,
          },
          {
            value: '4',
            text: '',
          },
        ],
      ],
      selectorValue: '',
    }
  },
  methods: {
    showSelector() {
      this.isSelectorShow = true
    },
    onSelectorConfirm({text}) {
      this.selectorValue = text
    },
  },
}
// #region ignore
export const metaInfo = {
  'zh-CN': {
    title: 'Check',
  },
  'en-US': {
    title: 'Check mode',
  },
}
