<template>
  <div class="md-example-child md-example-child-dialog md-example-child-dialog-0">
    <md-button inline size="small" @click="basicDialog.open = true"></md-button>
    <md-button inline size="small" @click="iconDialog.open = true"></md-button>
    <md-button inline size="small" @click="warnDialog.open = true"></md-button>
    <md-button inline size="small" @click="asyncDialog.open = true"></md-button>
    <md-button inline size="small" @click="actDialog.open = true"></md-button>
    <md-button inline size="small" @click="slotDialog.open = true"></md-button>

    <md-dialog
      title=""
      :closable="true"
      v-model="basicDialog.open"
      :btns="basicDialog.btns"
    >
      
    </md-dialog>

    <md-dialog
      icon="location"
      :closable="true"
      v-model="iconDialog.open"
      :btns="iconDialog.btns"
    >
      
    </md-dialog>

    <md-dialog
      title=""
      :closable="false"
      v-model="warnDialog.open"
      :btns="warnDialog.btns"
    >
      
    </md-dialog>

    <md-dialog
      :closable="false"
      v-model="asyncDialog.open"
      :btns="asyncDialog.btns"
      @click="cc"
    >
       
    </md-dialog>

    <md-dialog
      title=""
      :closable="false"
      layout="column"
      v-model="actDialog.open"
      :btns="actDialog.btns"
    >
      
    </md-dialog>

    <md-dialog
      title=""
      :closable="false"
      v-model="slotDialog.open"
      :btns="slotDialog.btns"
    >
      <div class="dialog-banner" slot="header">
        <img mode="widthFix" src="http://img-hxy021.didistatic.com/static/strategymis/insurancePlatform_spu/uploads/27fb7f097ca218d743f816836bc7ea4a" alt="">
      </div>
      
    </md-dialog>
  </div>
</template>

<script>
import Dialog from 'mand-mobile/lib/dialog/dialog'
import Button from 'mand-mobile/lib/button'
import Toast from 'mand-mobile/lib/toast'

export default {
  components: {
    'md-dialog': Dialog,
    'md-button': Button,
  },
  data() {
    return {
      basicDialog: {
        open: false,
        btns: [
          {
            text: '',
            handler: this.onBasicCancel,
          },
          {
            text: '',
            handler: this.onBasicConfirm,
          },
        ],
      },
      iconDialog: {
        open: false,
        btns: [
          {
            text: '',
            handler: this.onIconConfirm,
          },
        ],
      },
      warnDialog: {
        open: false,
        btns: [
          {
            text: '',
          },
          {
            text: '',
            warning: true,
            handler: this.onWarnConfirm,
          },
        ],
      },
      asyncDialog: {
        open: false,
        btns: [
          {
            text: '',
            icon: 'search',
            handler: this.onAsyncConfirm,
          },
        ],
      },
      actDialog: {
        open: false,
        btns: [
          {
            text: '',
            type: 'danger',
            handler: this.onActConfirm,
          },
          {
            text: '',
            handler: this.onActConfirm,
            disabled: true,
          },
          {
            text: '',
            handler: this.onActConfirm,
          },
        ],
      },
      slotDialog: {
        open: false,
        btns: [
          {
            text: '',
          },
        ],
      },
    }
  },
  methods: {
    cc() {},
    onBasicConfirm() {
      Toast.create({
        content: '',
      })
      this.basicDialog.open = false
    },
    onBasicCancel() {
      Toast.create({
        content: '',
      })
      this.basicDialog.open = false
    },
    onIconConfirm() {
      Toast.create({
        content: '',
      })
      this.iconDialog.open = false
    },
    onActConfirm() {
      this.actDialog.open = false
    },
    onAsyncConfirm(btn) {
      this.$set(btn, 'loading', true)
      this.$set(btn, 'text', '')

      setTimeout(() => {
        this.asyncDialog.open = false
        this.$set(btn, 'loading', false)
        this.$set(btn, 'text', '')
        Toast.create({
          content: '',
        })
      }, 1500)
    },
  },
}

</script>

<style>
.dialog-banner img {
  display: block;
  width: 100%;
}
    
.md-button {
  display: block;
  margin-bottom: 16px;
}
</style>
